.is-hidden {
  visibility: hidden;
  display: none;
}

.loader {
  display: inline-block;
  position: relative;
}

.loader-span:before {
  content: "Loading";
  color: #fff;
  letter-spacing: 2px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  animation: floating 1s ease-out infinite alternate;
  display: inline-block;
}

.loader-span:after {
  content: "";
  width: 100%;
  height: 10px;
  filter: blur(4px);
  box-sizing: border-box;
  background: rgba(0, 0, 0, .15);
  border-radius: 50%;
  animation: animloader 1s ease-out infinite alternate;
  position: absolute;
  top: 100%;
  left: 0;
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-25px);
  }
}

@keyframes animloader {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1.2);
  }
}

